import React, { Component } from 'react';
import { NavLink, Route, Switch } from 'react-router-dom';
import styled from 'styled-components';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

import menuNavData from '../data/menuNavData';
import menuData from '../data/menuData';
import MenuContents from '../components/MenuContents';
import HeaderStyles from '../styles/HeaderStyles';
import FullWidthContainer from '../styles/FullWidthContainer';
import SeoulContainer from '../styles/SeoulContainer';
import SeoulContainerM from '../styles/SeoulContainerM';

const MenuNavbar = styled.nav`
  z-index: 1;
  left: 0;
  bottom: 0;
  position: absolute;
  /* width: ${props => props.width}px; */
  width: 100%;
  /* position: ${props => (props.fixed ? 'fixed' : 'absolute')}; */
  /* ${props => props.fixed && `top: ${props.mainNavHeight}px;`} */
  height: 54px;
  /* background: ${props => props.theme.lightOrange}; */
  background: ${props => (props.fixed ? props.theme.lightOrange : 'none')};
  transition: all 300ms;
  display: flex;
  align-items: center;
  /* &.fixed {
    position: fixed;
  } */
  * {
    overflow: hidden;
  }
  .slider-wrapper {
    z-index: 2;
    display: flex;
    justify-content: center;
    height: 48px;
      .slider-inner {
        z-index: 4;
        background: ${props => props.theme.lightGrey};
        position: relative;
        overflow-x: scroll;
        height: 70px;
        width: 640px;
        ul.slider {
          z-index: 5;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: 'flex-start';
          flex-wrap: nowrap;
          left: ${props => (props.shouldBeCentered ? '50%' : 0)};
          transform: ${props =>
            props.shouldBeCentered ? 'translateX(-50%)' : 9};
          position: absolute;
          height: 70px;
          /* top: -11px; */
          top: -11px;
          li {
            margin-left: 6px;
            border-radius: 20px;
            height: 40px;
            padding: 2px 0;
            display: flex;
            justify-content: center;
            align-items: center;
            &:last-child {
              margin-right: 6px;
            }
            a {
              width: 100%;
              height: 100%;
              font-family: 'Leckerli One', cursive;
              /* font-family: 'Amatic SC', cursive; */
              font-size: 16px;
              /* font-weight: bold; */
              /* padding: 6px 8px; */
              padding: 0 10px;
              height: 40px;
              display: flex;
              align-items: center;
              border-radius: inherit;
              transition: all 300ms ease-in-out;
              white-space: nowrap;
              color: ${props => props.theme.darkRed};
              &.menu-nav-active {
                /* background: ${props => props.theme.lightRed}; */
                background: ${props => props.theme.darkRed};
                color: ${props => props.theme.offWhite};
              } 
            }
          }
        
      }
    }
  }
`;

const RouteAnimation = styled.div`
  position: relative;
  .fadeMenu {
  }
  .fadeMenu-enter {
    opacity: 0;
  }

  .fadeMenu-enter.fadeMenu-enter-active {
    opacity: 1;
    /* transition: opacity 400ms linear 200ms; */
    transition: all 400ms;
  }
  .fadeMenu-exit {
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
  }
  .fadeMenu-exit.fadeMenu-exit-active {
    opacity: 0;
    /* transition: opacity 200ms linear; */
    transition: all 400ms;
  }
`;

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fixed: false,
      width: 0
    };
    this.menuNav = React.createRef();
    this.handleMenuNav = this.handleMenuNav.bind(this);
  }
  componentDidMount() {
    this.handleMenuNav();
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.fixed !== prevState.fixed) {
      return { fixed: nextProps.fixed };
    }
    if (nextProps.width !== prevState.width) {
      return { width: nextProps.width };
    }
    return false;
  }

  handleMenuNav = () => {
    const { width } = this.props;
    this.setState({ fixed: false, width });
    const menuNavOffsetTop = this.menuNav.current.offsetTop;
    this.props.menuNavbar(menuNavOffsetTop);
  };

  render() {
    // const { fixed, width } = this.state;
    const { fixed, width } = this.props;
    const { mainNavHeight } = this.props;
    const style = {
      width: `${width}px`
    };
    const fixedStyle = {
      width: `${width}px`,
      top: mainNavHeight,
      position: 'fixed'
    };
    const shouldBeCentered = width < 513 ? false : true;
    // console.log(menuData[0].data.map(item => console.log(item)));
    // console.log(fixed);
    return (
      <>
        <HeaderStyles
          // backgroundUrl="/images/menu/menu-header-bg2.jpg"
          backgroundUrl="/images/menu/kimchi-backgroundDark.jpg"
          className="menuHeader"
        >
          <MenuNavbar
            ref={this.menuNav}
            style={fixed ? fixedStyle : style}
            backgroundFixed={fixed}
            width={width}
            fixed={fixed}
            mainNavHeight={mainNavHeight}
            shouldBeCentered={shouldBeCentered}
            className="menuNav"
            // className={'menuNav ' + (fixed && 'fixed')}
          >
            <SeoulContainer className="slider-wrapper">
              <div className="slider-inner">
                <ul className="slider">
                  {menuNavData.map((item, i) => (
                    <li key={i}>
                      <NavLink
                        to={item.url}
                        activeClassName="menu-nav-active"
                        key={item.id}
                      >
                        {item.name}
                      </NavLink>
                    </li>
                  ))}
                </ul>
              </div>
            </SeoulContainer>
          </MenuNavbar>
        </HeaderStyles>
        <FullWidthContainer
          backgroundUrl="/images/menu-outer-bg.gif"
          backgroundRepeat="repeat"
          padding="1rem 0 1rem 0"
        >
          <SeoulContainerM backgroundColor="#fff" padding="2rem">
            <Route
              render={({ location }) => (
                <RouteAnimation>
                  <TransitionGroup>
                    <CSSTransition
                      // unmountOnExit
                      // className="fadeMenu"
                      classNames="fadeMenu"
                      key={location.key}
                      // timeout={{ enter: 4000, exit: 4000 }}
                      timeout={400}
                    >
                      <Switch location={location}>
                        {menuData.map((item, i) => (
                          <Route
                            key={item.path}
                            exact
                            path={item.path}
                            render={() => (
                              <MenuContents
                                key={item.id}
                                data={item}
                                fixed={fixed}
                              />
                            )}
                          />
                        ))}
                      </Switch>
                    </CSSTransition>
                  </TransitionGroup>
                </RouteAnimation>
              )}
            />
          </SeoulContainerM>
        </FullWidthContainer>
        <div style={{ minHeight: '100vh' }} className="konto" />
        {/* <div>
          {Array.apply(null, Array(30)).map((item, i) => (
            <h1 key={i}>Test</h1>
          ))}
        </div> */}
      </>
    );
  }
}

export default Menu;
