import React from 'react';
import styled from 'styled-components';

const Top = styled.a`
  z-index: 3;
  position: fixed;
  right: -10%;
  bottom: -10%;
  transition: all 500ms;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(35, 45, 62, 0.7);
  color: #fff;

  cursor: pointer;
  transition: all 300ms ease;
  display: flex;
  justify-content: center;
  align-items: center;
  &:hover {
    background: rgba(35, 45, 62, 1);
    color: rgba(255, 255, 255, 1);
  }
  &:active {
    transform: scale(0.8);
  }
  .fa-chevron-up {
    color: #fff;
  }

  /* z-index: 3;
  opacity: 0.3;
  background-color: #cf3472;
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: -10%;
  right: -10%;
  border-radius: 5px;
  border: none;
  outline: none !important;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 300ms ease-in-out;
  &:active {
    transform: scale(0.9);
  }
  &:hover {
    opacity: 1;
  }
  .fa-chevron-up {
    color: #fff;
  } */

  ${({ active }) => active && `right: 2%; bottom: 2%`}
`;

const ToTop = props => (
  <Top active={props.active} onClick={props.totoTop}>
    <i className="fas fa-chevron-up" />
  </Top>
);

export default ToTop;
